import { API } from '@/shared/config'
import requestInterceptor from '@/shared/requestInterceptor'
import { parseAxiosErrorToAppError } from '@/shared/errorHelper'

async function getUserById (userId) {
  try {
    const response = await requestInterceptor.get(`${API}/users/${userId}`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getLikedTrails (userId) {
  try {
    const response = await requestInterceptor.get(`${API}/likes/${userId}`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function likeTrail (idOfUser, idOfTrail) {
  try {
    const response = await requestInterceptor.post(`${API}/likes/`, { userId: idOfUser, trailId: idOfTrail })
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function unLikeTrail (likeId) {
  try {
    const response = await requestInterceptor.delete(`${API}/likes/${likeId}`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

export const userService = {
  getUserById,
  getLikedTrails,
  likeTrail,
  unLikeTrail
}
