<template>
  <div id="info" class="container">
    <div class="row">
      <div class="col">
        <h5>Parc: {{ park.name }}</h5>
      </div>
      <div class="col">
        <h5>Sentier: {{ trail.name }}</h5>
      </div>
    </div>
    <div class="row align-items-center">
      <img v-on:click="dislike()" v-if="this.isLiked" src="../assets/like.png" />
      <img v-on:click="like()" v-else src="../assets/dislike.png" />
      <h5>J'aimes: {{ nbLikes }}</h5>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    park () {
      if (this.$store.state.trailInfo.selectedPark != null) {
        return this.$store.state.trailInfo.selectedPark
      }
      return 'Aucun'
    },
    trail () {
      if (this.$store.state.trailInfo.selectedTrail != null) {
        this.getLikes()
        return this.$store.state.trailInfo.selectedTrail
      }
      return 'Aucun'
    },
    nbLikes () {
      if (this.$store.state.trailInfo.likesOfTrail != null) {
        return this.$store.state.trailInfo.likesOfTrail.length
      }
      return 0
    },
    isLiked () {
      if (this.$store.state.trailInfo.likesOfTrail != null && this.$store.state.authentication.token !== '') {
        return this.getLikedId(this.$store.state.trailInfo.likesOfTrail) != null
      }
      return false
    },
    onError () {
      return this.$store.state.profile.onError
    }
  },
  methods: {
    async like () {
      if (this.trail.id != null && this.$store.state.authentication.token !== '') {
        const userId = this.$store.getters['authentication/getTokenUserId']
        await this.$store.dispatch('profile/like', { userId: userId, trailId: this.trail.id })
        this.getLikes()
        if (this.onError) {
        // todo
          console.log('TODO: afficher une erreur')
        }
      }
    },
    async dislike () {
      if (this.$store.state.trailInfo.likesOfTrail != null && this.$store.state.authentication.token !== '') {
        await this.$store.dispatch('profile/dislike', { likeId: this.getLikedId() })
        this.getLikes()
        if (this.onError) {
          // todo
          console.log('TODO: afficher une erreur')
        }
      }
    },
    async getLikes () {
      await this.$store.dispatch('trailInfo/getLikesOfTrailAction')
    },
    getLikedId () {
      const likesOfTrail = this.$store.state.trailInfo.likesOfTrail
      const userId = this.$store.getters['authentication/getTokenUserId']
      let likeId = null
      likesOfTrail.forEach(function (like) {
        if (like.userId === userId) {
          likeId = like.id
        }
      })
      return likeId
    }
  }
}
</script>

<style scoped>
h5 {
  color: white;
}
</style>
