import Vue from 'vue'
import Vuex from 'vuex'
import authenticationStore from './modules/authenticationStore'
import profileStore from './modules/profileStore'
import trailInfoStore from './modules/trailInfoStore'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {},
  mutations: {},
  actions: {},
  modules: {
    authentication: authenticationStore,
    profile: profileStore,
    trailInfo: trailInfoStore
  }
})
