<template>
  <div style="background: green; padding:10px" class="container">
    <l-map style="height:600px; width:100%;" :zoom="zoom" :center="center">
      <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
      <l-polyline
        v-for="polyline in this.polylines"
        v-bind:key="polyline.id"
        :lat-lngs="polyline.latlngs"
        :color="polyline.color"
      ></l-polyline>
    </l-map>
  </div>
</template>

<script>
import { LMap, LTileLayer, LPolyline } from 'vue2-leaflet'

export default {
  components: {
    LMap,
    LTileLayer,
    LPolyline
  },
  async created () {},
  data () {
    return {
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
        '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      zoom: 10
    }
  },
  computed: {
    center () {
      return this.$store.state.trailInfo.center
    },
    polylines () {
      return this.$store.state.trailInfo.polylines
    }
  }
}
</script>
