<template>
  <div id="selector" class="container">
    <h5>Sélectionnez un parc:</h5>
    <select
      v-model="selectedParkName"
      v-on:change="selectPark()"
      class="form-control"
      id="parks"
    >
      <option v-for="park in this.parks" v-bind:key="park.id">
        {{ park.name }}
      </option>
    </select>
    <br />
    <h5>Sélectionnez un sentier:</h5>
    <select
      v-model="selectedTrailName"
      v-on:change="trailSelect()"
      class="form-control"
      id="trails"
    >
      <option v-for="trail in this.trails" v-bind:key="trail.id">
        {{ trail.name }}
      </option>
    </select>
    <br />
  </div>
</template>

<script>
export default {
  data () {
    return {
      parksLoading: true,
      selectedTrailName: '',
      selectedParkName: '',
      trailsLoading: true
    }
  },
  computed: {
    parks () {
      return this.$store.state.trailInfo.parks
    },
    trails () {
      return this.$store.state.trailInfo.trails
    }
  },
  async created () {
    await this.loadParks()
  },
  methods: {
    async loadParks () {
      try {
        await this.$store.dispatch('trailInfo/getParksAction')
        this.parksLoading = false
      } catch (error) {
        this.$bvModal.msgBoxOk('Erreur dans la requête des parcs.', {
          okTitle: 'Ok'
        })
      }
    },
    async trailSelect () {
      await this.$store.dispatch(
        'trailInfo/trailSelectAction',
        this.selectedTrailName
      )
    },
    async loadTrails (parkId) {
      try {
        await this.$store.dispatch('trailInfo/getTrailsAction', parkId)
        this.trailsLoading = false
      } catch (error) {
        this.$bvModal.msgBoxOk('Erreur dans la requête des sentiers.', {
          okTitle: 'Ok'
        })
      }
    },
    async selectPark () {
      this.$store.commit('trailInfo/setSelectedPark', this.selectedParkName)
      await this.loadTrails(this.$store.state.trailInfo.selectedPark.id)
    }
  }
}
</script>

<style scoped>
h5 {
  color: white;
}
</style>
