<template>
  <nav class="navbar navbar-expand-md navbar-dark bg-dark">
    <div class="navbar-nav mr-auto">
      <router-link class="nav-link" v-bind:to="{ name: 'Home' }"
        >Accueil</router-link
      >
    </div>
    <div class="navbar-nav ml-auto">
      <b-link @click="logout" v-if="isLoggedIn" class="nav-link">
        Se déconnecter
      </b-link>

      <router-link class="nav-link" v-else v-bind:to="{ name: 'Login' }">
        Connexion
      </router-link>

      <router-link
        class="nav-link"
        v-if="isLoggedIn == false"
        v-bind:to="{ name: 'Register' }"
      >
        Inscription
      </router-link>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'Navigationbar',
  computed: {
    isLoggedIn () {
      return this.$store.getters['authentication/isLoggedIn']
    }
  },
  methods: {
    logout () {
      this.$store.dispatch('authentication/logout')
      this.$router.push({
        name: 'Login'
      })
    }
  }
}
</script>
