import { API } from '@/shared/config'
import * as axios from 'axios'

async function getTrailsByPark (parkId) {
  const response = await axios.get(`${API}/parks/${parkId}/trails`)
  return response.data
}

async function getSegmentsById (segmentId) {
  const response = await axios.get(`${API}/segments/${segmentId}`)
  return response.data
}

async function getLikesByTrailId (trailId) {
  const response = await axios.get(`${API}/trails/${trailId}/likes`)
  return response.data
}

export const trailsService = {
  getTrailsByPark,
  getSegmentsById,
  getLikesByTrailId
}
