import { latLng } from 'leaflet'
import { trailsService } from '../../services/trailsService'
import { parksService } from '../../services/parksService'

const state = {
  selectedTrail: {}, // setSelectedTrail
  onError: false,
  center: latLng(46.829853, -71.254028), // center
  parks: [], // allParks
  trails: [], // trailsForPark
  selectedPark: {}, // setSelectedPark
  likesOfTrail: [],
  polylines: []
}

const getters = {}

const mutations = {
  setOnError: state => {
    state.onError = true
  },
  center (state, center) {
    state.center = latLng(center)
  },
  setParks (state, parks) {
    state.parks = parks
  },
  setSelectedPark (state, parkName) {
    state.parks.forEach(element => {
      if (element.name === parkName) {
        state.selectedPark = element
      }
    })
  },
  setTrails (state, trails) {
    state.trails = trails
  },
  setLikesOfTrail (state, likesOfTrail) {
    state.likesOfTrail = likesOfTrail
  },
  setSelectedTrail (state, trail) {
    state.selectedTrail = trail
  },
  setPolylines (state, polylines) {
    state.polylines = polylines
  }
}

const actions = {
  async getParksAction ({ commit }) {
    // setParks, setSelectedPark
    try {
      const parks = await parksService.getParks()
      commit('setParks', parks)
      commit('setSelectedPark', state.parks[0].name)
    } catch (error) {
      commit('setOnError')
    }
  },
  async getTrailsAction ({ commit }) {
    // setTrails
    try {
      const trails = await trailsService.getTrailsByPark(state.selectedPark.id)
      commit('setTrails', trails)
    } catch (error) {
      commit('setOnError')
    }
  },
  async trailSelectAction ({ commit }, selectedTrailName) {
    // setSelectedTrail, center
    try {
      // Selected trail name into object
      for (const index in state.trails) {
        if (state.trails[index].name === selectedTrailName) {
          commit('setSelectedTrail', state.trails[index])
          break
        }
      }

      // Center
      const firstSegment = await trailsService.getSegmentsById(
        state.selectedTrail.segments[0]
      )
      commit('center', firstSegment.coordinates[0])

      // Polylines coordinates
      const polylines = []
      for (const index in state.selectedTrail.segments) {
        const segment = await trailsService.getSegmentsById(
          state.selectedTrail.segments[index]
        )

        const latlngs = []
        for (const coordinates in segment.coordinates) {
          latlngs.push(segment.coordinates[coordinates])
        }

        let color = 'black'
        if (segment.levelId === 1) {
          color = 'green'
        }
        if (segment.levelId === 0) {
          color = 'blue'
        }
        if (segment.levelId === 2) {
          color = 'orange'
        }
        if (segment.levelId === 4) {
          color = 'red'
        }

        polylines.push({
          id: segment.id,
          latlngs: latlngs,
          color: color
        })
      }
      commit('setPolylines', polylines)
    } catch (error) {
      commit('setOnError')
    }
  },
  async getLikesOfTrailAction ({ commit }) {
    try {
      const likesOfTrail = await trailsService.getLikesByTrailId(
        state.selectedTrail.id
      )
      commit('setLikesOfTrail', likesOfTrail)
    } catch (error) {
      commit('setOnError')
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
